import React, { useState, useEffect } from "react";
import style from "./CreateSubject.module.css";
import { Dialog } from "@mui/material";
import { useTranslation } from "react-i18next";
import "../AddDisplay.css";
import {
  useOneDepartmentAddSubjectClass,
  useOneDepartmentAllSubjectMaster,
  useOneDepartmentAllSubjectOneClass,
  useOneDepartmentClassProfile,
  useOneSubjectEdit,
} from "../../../../Department/Institute/Components/DepartmentApi/depart-api";
import { imageShowUrl } from "../../../../services/BaseUrl";
import LoaderButton from "../../../../Loader/LoaderButton";
import AssignStaffModal from "../../../../Department/Institute/AssignStaff/AssignStaffModal";
import StringLength from "../../../../Custom/Validation/StringLength";
import MarkValue from "../../../../Custom/Validation/MarkValue";
import JoinFormInput from "../../../../JoiningForm/Student/Form/JoinFormInput";
import CustomSelectDepartment from "../../../../JoiningForm/Student/Form/CustomSelectDepartment";
import { useGetClassAllBatch } from "../../../../hooks/member_tab/class-api";

function Options({ options, switchOption }) {
  return (
    <div className={style.profileToolTip}>
      <div className={style.triangle}></div>
      <div className={style.profilelist}>
        {options?.map((option, index) => (
          <div
            value={option._id}
            onClick={() => switchOption(option)}
            key={index}
          >
            {option.subjectName}{" "}
            {option?.subjectType === "Optional" && `(${option?.subjectType})`}
          </div>
        ))}
      </div>
    </div>
  );
}

function CreateSubject({
  departmentModal,
  hideModal,
  id,
  did,
  bid,
  cid,
  edit,
  setEdit,
  editSubject,
}) {
  const { t } = useTranslation();
  const [click, setClick] = useState(false);
  const [classs, setClasss] = useState(
    (edit && editSubject?.subjectName) || t("please_select_subject_")
  );
  const [disabled, setDisabled] = useState(false);
  const [fillAllDetail, setFillAllDetail] = useState(true);
  const [markValidation, setMarkValidation] = useState(false);
  const [selectOptions, setSelectOptions] = useState([]);
  const [assignHead, setAssignHead] = useState(false);
  const [batch, setBacth] = useState("Full Class");
  const [batchArr, setBatchArr] = useState([]);
  const [filled, setFilled] = useState("");
  const [assignHeadStaff, setAssignHeadStaff] = useState(
    edit && editSubject?.subjectTeacherName
  );
  const [createClass, setCreateClass] = useState({
    msid: (edit && editSubject?.subjectMasterName) || "",
    subjectTitle: (edit && editSubject?.subjectTitle) || "",
    sid: (edit && editSubject?.subjectTeacherName?._id) || "",
    subjectPassingMarks:
      (edit && editSubject?.setting?.subjectPassingMarks) || 0,
    lecture_analytic: {
      lecture_count:
        (edit && editSubject?.lecture_analytic?.lecture_count) || "",
      lecture_time: (edit && editSubject?.lecture_analytic?.lecture_time) || "",
    },
    practical_analytic: {
      practical_count:
        (edit && editSubject?.practical_analytic?.practical_count) || "",
      practical_time:
        (edit && editSubject?.practical_analytic?.practical_time) || "",
    },
    tutorial_analytic: {
      tutorial_count:
        (edit && editSubject?.tutorial_analytic?.tutorial_count) || "",
      tutorial_time:
        (edit && editSubject?.tutorial_analytic?.tutorial_time) || "",
    },
  });

  const { allSubjectMaster } = useOneDepartmentAllSubjectMaster({
    data: {
      id: id,
      did: did,
    },
    skip: !id,
  });
  const [addSubjectInClass] = useOneDepartmentAddSubjectClass();
  const { oneClassAllSubjectRefetch } = useOneDepartmentAllSubjectOneClass({
    cid: cid,
    skip: false,
  });
  const { oneClassProfileRefetch } = useOneDepartmentClassProfile({
    cid: cid,
    skip: false,
  });
  const { getClassAllBatch, getClassAllBatchRefetch } = useGetClassAllBatch({
    cid: cid,
    skip: batch === "Particular Batch" ? !cid : true,
  });
  const [subjectEdit] = useOneSubjectEdit();
  //FOr edit of class detail
  useEffect(() => {
    if (edit && editSubject) {
      setCreateClass({
        msid: editSubject?.subjectMasterName || "",
        subjectTitle: editSubject?.subjectTitle || "",
        sid: editSubject?.subjectTeacherName?._id || "",
        subjectPassingMarks: editSubject?.setting?.subjectPassingMarks || 0,
        lecture_analytic: {
          lecture_count: editSubject?.lecture_analytic?.lecture_count || "",
          lecture_time: editSubject?.lecture_analytic?.lecture_time || "",
        },
        practical_analytic: {
          practical_count:
            editSubject?.practical_analytic?.practical_count || "",
          practical_time: editSubject?.practical_analytic?.practical_time || "",
        },
        tutorial_analytic: {
          tutorial_count: editSubject?.tutorial_analytic?.tutorial_count || "",
          tutorial_time: editSubject?.tutorial_analytic?.tutorial_time || "",
        },
      });
      setClasss(editSubject?.subjectName || t("select_standerd_"));
      setAssignHeadStaff(editSubject?.subjectTeacherName);
    }
  }, [edit, editSubject]);

  useEffect(() => {
    if (cid && batch === "Particular Batch") {
      getClassAllBatchRefetch();
    }
  }, [batch, cid, getClassAllBatchRefetch]);
  useEffect(() => {
    if (allSubjectMaster?.subjectMaster) {
      setSelectOptions(allSubjectMaster?.subjectMaster);
    }
  }, [allSubjectMaster]);

  const classheadSelect = (assing) => {
    setAssignHeadStaff(assing);
    setCreateClass({ ...createClass, sid: assing._id });
    setFillAllDetail(true);
  };
  const switchClass = (option) => {
    if (option?.subjectType === "Optional")
      setClasss(`${option.subjectName} (${option?.subjectType})`);
    else setClasss(option.subjectName);
    setCreateClass({ ...createClass, msid: option._id });
    setFillAllDetail(true);
  };

  const onResetState = () => {
    setCreateClass({
      msid: "",
      subjectTitle: "",
      sid: "",
      subjectPassingMarks: 0,
      lecture_analytic: {
        lecture_count: "",
        lecture_time: "",
      },
      practical_analytic: {
        practical_count: "",
        practical_time: "",
      },
      tutorial_analytic: {
        tutorial_count: "",
        tutorial_time: "",
      },
    });
    setBatchArr([]);
    setBacth("");
  };
  const subjectSubmitHandler = () => {
    if (
      createClass.msid &&
      // && createClass.sid
      createClass.subjectTitle
    ) {
      if (!edit) {
        let flag = true;
        if (batch) {
          if (batch === "Particular Batch" && !batchArr?.length) {
            setFilled({ batchList: "* required" });
            flag = false;
          }
        } else {
          setFilled({ batch: "* required" });
          flag = false;
        }

        if (flag) {
          setDisabled(true);
          addSubjectInClass({
            subjectAdd: {
              ...createClass,
              batch_arr: batchArr?.[0]?._id ? [batchArr?.[0]?._id] : [],
            },
            id: id,
            did: did,
            bid: bid,
            cid: cid,
          })
            .then((res) => {
              if (res) {
                hideModal(false);
                setDisabled(false);
                onResetState();
                setAssignHeadStaff("");
                oneClassProfileRefetch();
                oneClassAllSubjectRefetch();
              }
            })
            .catch({});
        }
      } else {
        setDisabled(true);

        const subjectDetail = {
          smId:
            editSubject?.subjectMasterName === createClass.msid
              ? ""
              : createClass.msid,
          subjectTitle:
            editSubject?.subjectTitle === createClass.subjectTitle
              ? ""
              : createClass.subjectTitle,
          sid:
            editSubject?.subjectTeacherName?._id === createClass.sid
              ? ""
              : createClass.sid,
          subjectPassingMarks: +createClass.subjectPassingMarks,
          lecture_analytic: {
            lecture_count: createClass.lecture_analytic.lecture_count,
            lecture_time: createClass.lecture_analytic.lecture_time,
          },
          practical_analytic: {
            practical_count: createClass.practical_analytic.practical_count,
            practical_time: createClass.practical_analytic.practical_time,
          },
          tutorial_analytic: {
            tutorial_count: createClass.tutorial_analytic.tutorial_count,
            tutorial_time: createClass.tutorial_analytic.tutorial_time,
          },
        };
        subjectEdit({
          subject: subjectDetail,
          sid: editSubject?._id,
        })
          .then(() => {
            hideModal(false);
            setDisabled(false);
            setEdit(false);
            onResetState();
            setAssignHeadStaff("");
            oneClassAllSubjectRefetch();
          })
          .catch({});
      }
    } else {
      setFillAllDetail(false);
    }
  };

  const onClose = () => {
    hideModal(false);
    setEdit(false);
    setClasss(t("please_select_subject_"));
    onResetState();
    setAssignHeadStaff("");
  };

  const onBatchType = (value) => {
    setBacth((prev) => (prev = value));
  };

  const onBatchSelect = (val) => {
    setBatchArr(() => [val]);
  };
  return (
    <>
      <Dialog open={departmentModal}>
        <div className={style.DModal}>
          <div className={style.title}>
            {!edit ? (
              <h5>{t("add-subject-n-place")}</h5>
            ) : (
              <h5>Edit Subject</h5>
            )}

            <img src="/images/close-post-icon.svg" onClick={onClose} alt="" />
          </div>
          <div hidden={fillAllDetail} className={style.notFound}>
            {t("fill_all_fields_to_add_subject")}
          </div>
          <div className={style.item} onClick={() => setClick(!click)}>
            <h6>{t("select-subject-place")}</h6>
            <div className={style.select}>
              <p>{classs}</p>

              <img src="/images/select-icon.svg" alt="" />
            </div>

            {click && (
              <div className={style.tooltip}>
                <Options options={selectOptions} switchOption={switchClass} />
              </div>
            )}
          </div>
          <div className={style.mid} onClick={() => setAssignHead(true)}>
            <img
              src={
                assignHeadStaff?.staffProfilePhoto
                  ? `${imageShowUrl}/${assignHeadStaff.staffProfilePhoto}`
                  : "/images/avatar-icon.png"
              }
              alt=""
            />
            <div className={style.text}>
              <h6>
                {assignHeadStaff
                  ? `${assignHeadStaff?.staffFirstName} ${
                      assignHeadStaff?.staffMiddleName || ""
                    } ${assignHeadStaff?.staffLastName}`
                  : `${t("assign_subject_head")} ${t("optional")}`}
              </h6>
              <p>{t("search_from_joined_list")}</p>
            </div>
          </div>
          <div className={style.input}>
            <h6>{t("give-s-head-title")}</h6>
            <input
              type="text"
              value={createClass.subjectTitle}
              onChange={(e) => {
                if (e.target.value?.length <= 40) {
                  setCreateClass({
                    ...createClass,
                    subjectTitle: e.target.value,
                  });
                  setFillAllDetail(true);
                }
              }}
              required
              placeholder={t("shead_example")}
            />
            <StringLength
              maxLength={40}
              valueLength={createClass.subjectTitle}
            />
          </div>
          {edit ? null : (
            <>
              <CustomSelectDepartment
                selectLabel={t("batch")}
                selectedValue={t("please_select_batch_full_or_particular")}
                options={["Full Class", "Particular Batch"]}
                onClick={onBatchType}
                viewAs="NORMAL_FILTER"
                defalutValue={batch}
                errorShow={filled["batch"]}
              />
              {batch === "Particular Batch" ? (
                <CustomSelectDepartment
                  selectLabel={t("select_batch")}
                  selectedValue={t("please_select_batch")}
                  options={getClassAllBatch?.all_batches ?? []}
                  onClick={onBatchSelect}
                  viewAs="NORMAL_FILTER"
                  defalutValue={batchArr?.[0]?.batchName ?? ""}
                  openAs="PROMOTE"
                  errorShow={filled["batchList"]}
                />
              ) : null}
            </>
          )}
          <div className={style.input}>
            <h6>{t("subject_passing")}</h6>
            <input
              type="tel"
              value={createClass.subjectPassingMarks}
              onChange={(e) => {
                if (+e.target.value <= 100) {
                  setCreateClass({
                    ...createClass,
                    subjectPassingMarks: +e.target.value,
                  });
                  setFillAllDetail(true);
                } else {
                  setMarkValidation(true);
                }
              }}
              placeholder="E.g. 40"
            />
            {markValidation && (
              <MarkValue message={"Mark is not greater than 100"} />
            )}
          </div>

          <div className={style.academic_container}>
            <JoinFormInput
              labelText={t("total_lectures")}
              placeholder="60"
              name={"lecture_count"}
              type={"tel"}
              value={createClass.lecture_analytic.lecture_count}
              onChange={(e) =>
                setCreateClass((prev) => ({
                  ...prev,
                  lecture_analytic: {
                    ...prev.lecture_analytic,
                    lecture_count: +e.target.value,
                  },
                }))
              }
            />
            <JoinFormInput
              labelText={t("time_lectures")}
              placeholder="60"
              name={"lecture_time"}
              type={"tel"}
              value={createClass.lecture_analytic.lecture_time}
              onChange={(e) =>
                setCreateClass((prev) => ({
                  ...prev,
                  lecture_analytic: {
                    ...prev.lecture_analytic,
                    lecture_time: e.target.value,
                  },
                }))
              }
            />
          </div>
          <div className={style.academic_container}>
            <JoinFormInput
              labelText={t("practicals")}
              placeholder="60"
              name={"practical_count"}
              type={"tel"}
              value={createClass.practical_analytic.practical_count}
              onChange={(e) =>
                setCreateClass((prev) => ({
                  ...prev,
                  practical_analytic: {
                    ...prev.practical_analytic,
                    practical_count: +e.target.value,
                  },
                }))
              }
            />
            <JoinFormInput
              labelText={t("time_practical")}
              placeholder="60"
              name={"practical_time"}
              type={"tel"}
              value={createClass.practical_analytic.practical_time}
              onChange={(e) =>
                setCreateClass((prev) => ({
                  ...prev,
                  practical_analytic: {
                    ...prev.practical_analytic,
                    practical_time: e.target.value,
                  },
                }))
              }
            />
          </div>
          <div className={style.academic_container}>
            <JoinFormInput
              labelText={t("tutorials")}
              placeholder="60"
              name={"tutorial_count"}
              type={"tel"}
              value={createClass.tutorial_analytic.tutorial_count}
              onChange={(e) =>
                setCreateClass((prev) => ({
                  ...prev,
                  tutorial_analytic: {
                    ...prev.tutorial_analytic,
                    tutorial_count: +e.target.value,
                  },
                }))
              }
            />
            <JoinFormInput
              labelText={t("time_tutorial")}
              placeholder="60"
              name={"tutorial_time"}
              type={"tel"}
              value={createClass.tutorial_analytic.tutorial_time}
              onChange={(e) =>
                setCreateClass((prev) => ({
                  ...prev,
                  tutorial_analytic: {
                    ...prev.tutorial_analytic,
                    tutorial_time: e.target.value,
                  },
                }))
              }
            />
          </div>

          <button
            className={style.btn}
            onClick={subjectSubmitHandler}
            disabled={disabled}
          >
            {!edit ? <p>{t("create")}</p> : <p>{t("update")}</p>}
            {disabled && (
              <div>
                <LoaderButton />
              </div>
            )}
          </button>
        </div>
      </Dialog>

      {assignHead && (
        <AssignStaffModal
          assignHead={assignHead}
          headSelect={classheadSelect}
          title={t("assign_dhead")}
          onClose={() => setAssignHead((pre) => !pre)}
        />
      )}
    </>
  );
}

export default CreateSubject;
